.catalog-wrapper {
  display: grid;
  grid-template-columns: 250px 1fr;
  gap: 50px;

  @media (max-width: $tablet-width) {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    margin-bottom: 8px;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 5px;
    gap: 42px;
  }
}

.catalog__list {
  @include list-reset;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 9px 30px;

  @media (max-width: $mobile-width) {
    display: flex;
    flex-wrap: wrap;
    gap: 0;
  }
}

.catalog__item {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  text-align: center;
  margin-bottom: 30px;

  @media (max-width: $tablet-width) {
    margin-bottom: 27px;
  }

  @media (max-width: $mobile-width) {
    width: 45%;
    margin-bottom: 20px;
    padding-bottom: 15px;

    &:not(:nth-child(2n+2)) {
      margin-right: 10%;
    }
  }

  &:hover,
  &:focus {
    transform: scale(1.01);
    filter: drop-shadow(4px 4px 10px rgba(168, 122, 98, 0.24));
  }
}

.catalog a {
  display: block;
  width: 100%;
  color: inherit;
  text-decoration: none;
}

.catalog h4 {
  line-height: 21px;
  font-weight: 500;
  letter-spacing: 0.03em;
  margin: 0 auto 5px;
}

.catalog span {
  display: block;
  margin: 0 auto;
  font-weight: 700;

  @media (max-width: $mobile-width) {
    font-size: 14px;
  }
}

.catalog img {
  display: block;
  width: 100%;
  height: auto;
  max-width: 270px;
  max-height: 272px;
  margin-bottom: 22px;

  @media (max-width: $mobile-width) {
    margin-bottom: 16px;
  }
}
